.contact-page {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgb(44, 72, 72);
  padding: 50px 100px;
}
.error {
  color: #d75555;
  font-size: 16px;
}

.contact-sub-Head {
  font-family: "Inria Serif", serif;
  color: rgb(218, 111, 111);
}

.contact-Head {
  font-weight: 600;
  font-size: 40px;
  color: white;
  font-family: "Inria Serif", serif;
}
.social-medias ul {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.circle {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.handle-name {
  font-size: 25px;
  color: #d75555;
}

.social-handles {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.contact-form {
  background-color: #fff;
  padding: 50px 50px;
  width: 600px;
}
.contact-form-heading {
  font-size: 30px;
  font-weight: 500;
  font-family: "Inter", sans-serif;
}
.inputs-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px 0;
}
.Input-detail {
  font-size: 16px;
  font-family: "Inter", sans-serif;
  display: flex;
  width: 100%;
}
.inputs {
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-top: 40px;
}
.input-field {
  padding: 2px 10px;
  width: 60%;
}
.contact-button {
  margin-top: 50px;
  padding: 20px 30px;
  background-color: rgb(44, 72, 72);
  display: flex;
  justify-content: center;
  width: 200px;
  align-items: center;
  color: #fff;
  border-radius: 30px;
  cursor: default;
}

@media screen and (max-width: 500px) {
  .contact-form {
    width: 330px;
    padding: 20px 10px;
  }
  .contact-Head {
    font-size: 35px;
    padding: 0 0 20px 0;
  }
  .input-field {
    width: 50%;
  }
  .inputs-1 {
    gap: 20px;
    padding: 20px 0;
  }
  .inputs {
    gap: 20px;
  }
  .contact-button {
    margin-left: 0px;
    margin-top: 20px;
    width: 160px;
    font-size: 14px;
  }
  .contact-page {
    padding: 50px 40px;
  }
}
