@import url("https://fonts.googleapis.com/css2?family=Abel&family=Inria+Serif:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&family=Italiana&display=swap");
.fotter {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  color: white;
  gap: 60px;
  padding: 2% 8% 0% 8%;
}
.main-fotter {
  background-color: #2c8c87;
  padding: 20px;
  justify-content: center;
  align-items: center;
}
.fotter ul {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.fotter h3 {
  font-size: 30px;
  font-family: "Inria Serif", serif;
  font-weight: 600;
  font-style: normal;
  padding-bottom: 20px;
}
.fotter ul li {
  font-size: 13px;
}

.contact-1 {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.contact-options {
  gap: 5px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.boxes {
  width: 300px;
}
.F-button {
  border-radius: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 50px;
  color: white;
  background-color: #ea8080;
}
.copyright-box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 50px 0px 0px 0px;
  flex-direction: column;
}
.copyright-div {
  display: flex;
  gap: 2px;
  color: #fff;
  justify-content: center;
}
.line {
  width: 80%;
  height: 2px;
  background-color: #fff;
  margin-bottom: 20px;
}

.subscribe-text {
  font-family: "Inter", sans-serif;
  color: #b7b6b6;
  font-size: 14px;
}
.email-text {
  margin: 10px 0px 10px 0px;
  outline: none;
}

.button p {
  cursor: default;
}
.mail-text {
  text-decoration: none;
  color: white;
}

@media only screen and (max-width: 500px) {
  .boxes {
    width: 300px;
  }
  .fotter {
    padding: 0;
  }
  .thought {
    display: none;
  }
}
