.main {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
  width: 100%;
  flex-direction: column;
}
.main-header p {
  font-size: 30px;
  font-family: "Inria Serif", serif;
  font-weight: 900;
}
.lecters {
  padding: 50px 100px;
}
.lecter-box {
  width: 100%;
  background-color: #d6f0ee;
  margin-top: 40px;
  border-radius: 5px;
}

.lecter-box div {
  display: flex;
  justify-content: space-between;
  padding: 10px 40px;
}
.lecter-title {
  font-size: 20px;
  font-weight: 800;
  font-family: "Inria Serif", serif;
}
.lecter-button {
  padding: 10px 40px;
  background-color: #2c8c87;
  border-radius: 10px;
}
.lecter-box a {
  text-decoration: none;
  color: white;
}

@media screen and (max-width: 800px) {
  .lecter-box div {
    padding: 10px 10px;
  }

  .lecter-box {
    width: 350px;
  }
  .lecter-title {
    font-size: 16px;
  }
  .lecter-button {
    font-size: 13px;
  }
}
@media screen and (max-width: 500px) {
  .flex {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  .lecter-title {
    line-height: 24px;
  }
  .lecter-button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 10px;
    width: 100px;
  }
  .button-center-box {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
