.login-page {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(44, 72, 72);
  flex-direction: column;
}

.login-header {
  font-size: 35px;
  font-weight: 600;
  color: white;
  font-family: "Inria Serif", serif;
}
.login-sub-Head {
  font-family: "Inria Serif", serif;
  color: rgb(218, 111, 111);
}
.login-form {
  background-color: #fff;
}
.login-form form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  padding: 100px 50px;
  border-radius: 20px;
}

.login-detail {
  font-size: 16px;
  font-family: "Inter", sans-serif;
  display: flex;
  width: 100%;
}
.login-field {
  padding: 2px 10px;
  width: 60%;
}
.login-button1 {
  margin-top: 50px;
  padding: 10px 10px;
  background-color: rgb(44, 72, 72);
  display: flex;
  justify-content: center;
  width: 100px;
  align-items: center;
  color: #fff;
  border-radius: 30px;
  cursor: default;
}
.error {
  color: red;
}
