.navbar {
  width: 100%;
  display: flex;
  height: 80px;
  background-color: #d6f0ee;
  justify-content: space-between;
  align-items: center;
  padding: 0px 50px 0px 50px;
}

ul {
  gap: 40px;

  list-style: none;
  display: flex;
}
ul li {
  gap: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

ul li:hover a {
  color: #2c8c87;
  font-weight: 700;
}
ul li a {
  display: flex;
  justify-content: center;
  gap: 5px;
  align-items: center;
  padding-bottom: 2px;
  font-weight: 500;
  font-size: 16px;
  text-decoration: none;
  color: #1c1b1f;
}
ul li:hover .icon {
  color: #2c8c87;
}

.icon {
  padding-top: -2px;
  font-size: 22px;
  color: #1c1b1f;
}
.login-button {
  color: white;
  border-radius: 10px;
  background-color: #2c8c87;
  padding: 8px 35px 8px 35px;
}
.login-text {
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
}
.menu-link-mobile {
  display: none;
}
.menus-mobile {
  gap: 10px;
  display: flex;
  justify-content: start;
  align-items: start;
  padding: 10px 20px;
  flex-direction: column;
}
.site-name{
  font-size: 35px;
  color: black;
  font-weight: 700;
}
.navbar a{
  text-decoration: none;
}
@media only screen and (max-width: 500px) {
  .navbar {
    padding: 0px 20px;
  }
  .login-text-mobile {
    color: white;
    border-radius: 10px;
    background-color: #2c8c87;
    padding: 5px 10px;
    text-decoration: none;
  }

  .menu-link-desktop {
    display: none;
  }
  .menu-link-mobile {
    display: block;
  }
  .site-name {
    font-size: 25px;
  }
}
