.about{
   width: 100%;
   padding: 20px 20px;
   display: flex;
   justify-content: center;
   align-items: center;
   gap: 50px;
}

@media screen  and (max-width:2000px){
   .about-box{
      width: 800px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
   }
   
}

@media screen  and(max-width:500px) {
   .about-box{
      width: 320px;
   }
   
}