/* General reset for the page */
.not-found-container-1 {
   margin: 0;
   padding: 0;
   font-family: 'Arial', sans-serif;
   background: linear-gradient(135deg, #ff7eb3, #ff758c);
   color: #fff;
   height: 100%;
   overflow: hidden;
 }
 
 /* Main container */
 .not-found-container {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   text-align: center;
   height: 100vh;
   padding: 20px;
 }
 
 /* Animated 404 code */
 .error-code {
   font-size: 10rem;
   font-weight: bold;
   animation: float 3s ease-in-out infinite;
   text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.3);
 }
 
 /* Error message and call-to-action */
 .error-message h1 {
   font-size: 2.5rem;
   margin: 20px 0;
 }
 
 .error-message p {
   font-size: 1.2rem;
   margin-bottom: 30px;
 }
 
 .home-button {
   text-decoration: none;
   background: #fff;
   color: #ff758c;
   padding: 10px 25px;
   border-radius: 30px;
   font-size: 1rem;
   font-weight: bold;
   transition: all 0.3s ease-in-out;
   box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
 }
 
 .home-button:hover {
   background: #ff758c;
   color: #fff;
   transform: translateY(-3px);
   box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.3);
 }
 
 /* Floating animation for the error code */
 @keyframes float {
   0%, 100% {
     transform: translateY(0);
   }
   50% {
     transform: translateY(-20px);
   }
 }
 