@media screen and (max-width: 500px) {
  .video-section {
    width: 100%;
  }
  .floating-text {
    position: absolute;
    top: 10px; /* Adjust position */
    left: 10px; /* Adjust position */
    color: white;
    font-size: 16px;
    font-weight: bold;
    opacity: 0.7; /* Transparency for watermark effect */
    pointer-events: none; /* Prevent text from interfering with video controls */
    z-index: 5;
    animation: flow 20s linear infinite;
  }
  @keyframes flow {
    0% {
      transform: translateX(-100%);
      transform: translateY(-100%);
    }
    25% {
      transform: translateX(100%);
      transform: translateY(100%);
    }
    100% {
      transform: translateX(-100%);
      transform: translateY(-100%);
    }
  }
  video {
    width: 100%;
    height: auto;
  }
}
video::-webkit-media-controls-fullscreen-button {
  display: none;
}
@media screen and (max-width: 1100px) {
  video {
    width: 100%;
    height: auto;
  }
}
@media screen and (min-width: 2000px) {
  .floating-text {
    position: absolute;
    top: 20px; /* Adjust position */
    left: 20px; /* Adjust position */
    color: white;
    font-size: 24px;
    font-weight: bold;
    opacity: 1; /* Transparency for watermark effect */
    pointer-events: none; /* Prevent text from interfering with video controls */
    z-index: 10;
  }
}

.video-container {
  position: relative;
  display: inline-block;
}
video {
  width: 100%;
  height: auto;
}
