.course-box {
  display: flex;
  flex-wrap: wrap;
  gap: 100px;
  justify-content: center;
  align-items: center;
  padding-bottom: 100px;
}
.w-500 {
  width: 500px;
}
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.title {
  color: #023e17;
  font-size: 24px;
  font-family: "Inria Serif", serif;
  font-weight: 700;
}
.content-box {
  display: flex;
  flex-direction: column;
}
ol {
  padding: 50px 0px;
  list-style: disc;
}

ol li {
  padding-top: 8px;
}

.button-course {
  background-color: #2c8c87;
  padding: 16px 30px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.button-course a {
  text-decoration: none;
  color: white;
}

.Course-thumbnail {
  width: 500px;
}
@media only screen and (max-width: 1200px) {
  .Course-thumbnail {
    width: 500px;
  }
  .w-500 {
    width: 300px;
  }
  .course-box {
    display: flex;
    padding-bottom: 100px;
  }
  ol {
    padding: 15px 0px;
  }
}

@media screen and (max-width: 500px) {
  .w-500 {
    width: 300px;
  }
  ol {
    padding: 20px 0;
  }
  .Course-thumbnail {
    width: 300px;
  }
  .course-box {
    gap: 30px;
    padding-bottom: 50px;
  }
  .content-box {
    justify-content: start;
    align-items: start;
  }
}
