.section1 {
  width: 100%;
  background-image: url("../imgs/img.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.8;
  position: relative;
}
.remove_underline {
  text-decoration: none;
}
.section1-a {
  opacity: 1;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.section-sub-header {
  font-size: 20px;
  font-family: "Inter", sans-serif;
  font-weight: 200;
  color: white;
}
.section-header {
  font-size: 45px;
  font-family: "Inria Serif", serif;
  font-weight: 700;
  font-style: normal;
  color: white;
}
.section-about {
  width: 500px;
  text-align: center;
  font-family: "Inter", sans-serif;
  font-weight: 200;
  color: white;
}
.buttons {
  display: flex;
  padding-top: 50px;
  gap: 50px;
}
.button1 {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  border-radius: 30px;
  width: 150px;
  height: 50px;
  background-color: #6e94e6;
}

.button1:hover {
  background-color: #477cee;
}
.button2 {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #6e94e6;
  border-radius: 30px;
  width: 150px;
  height: 50px;
  border: white 2px solid;
}

.button2:hover .details {
  font-weight: 700;
}
.button1 p {
  cursor: default;
}

.button2 p {
  font-weight: 500;
  cursor: default;
}

.social-media {
  display: flex;
  justify-content: center;
  width: 100%;
  gap: 50px;
  display: flex;
  margin-top: -50px;
  position: absolute;
}
.Home-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  height: 100px;
  width: 100px;
}

.Course-detail {
  padding: 0px 10px;
}
.youtube {
  background-color: rgb(234, 226, 226);
}
.Home-circle:hover {
  margin-top: -20px;
}
.telegram {
  margin-top: -20px;
  background-color: #2aabee;
}
.facebook {
  background-color: #1877f2;
}

.section2 {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 150px 0;

  gap: 50px;
  background-image: url("../imgs/bac.jpg");
  background-size: contain;
}
.section2-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.section2-sub-head {
  color: #ea8080;
}
.section2-head {
  font-family: "Inria Serif", serif;
  font-weight: 900;
  font-style: normal;
  color: #023e17;
  font-size: 50px;
}
.section2-1-details {
  display: flex;
  flex-direction: column;
  gap: 10px;
  color: #888585;
  margin-top: 30px;
  margin-left: 50px;
}
.section2-button {
  font-family: "Inter", serif;
  border-radius: 30px;
  margin-top: 29px;
  background-color: #2c8c87;
  padding: 10px 25px 10px 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  box-shadow: 5px 5px 20px rgb(117, 112, 112);
}

.section3 {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 100px 0;
  flex-wrap: wrap;
  background-color: #d6f0ee;
}
.section3-content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 0 40px 50px 0;
}
.section3 ol {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-self: flex-start;
  gap: 10px;
  list-style: disc;
}
.sections-buttons {
  padding-top: 40px;
  display: flex;
  gap: 20px;
}

.sections3-button1 {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2c8c87;
  padding: 15px 25px 15px 25px;
  border-radius: 40px;
  color: white;
}

.sections3-button2 {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  padding: 15px 30px 15px 30px;
  border: #2c8c87 2px solid;
}
.sections3-button1 p {
  font-weight: 500;
}

.section4 {
  padding: 50px 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.section4-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.section4-box-sub-head {
  color: #ea8080;
  font-family: "Inter", serif;
  font-size: 14px;
}
.section4-box-head {
  color: #023e17;
  font-size: 40px;
  font-family: "Inria Serif", serif;
  font-weight: 600;
}
.section4-box-des {
  color: #888585;
  padding-top: 40px;
  font-size: 14px;
  width: 600px;
  text-align: center;
  padding-bottom: 60px;
}

.card {
  width: 300px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background-color: white;
  padding: 30px 50px 30px 50px;
  box-shadow: 3px 3px 20px 12px whitesmoke;
}

.card-image {
  position: relative;
  height: 100px;
  width: 100px;
  border-radius: 50%;
}

.card-image .card-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 50%;
}
.card-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.name {
  color: #023e17;
  font-family: "Italiana", serif;
  font-weight: 300;
  font-size: 20px;
  margin-top: 20px;
}
.Profession {
  padding-top: 5px;
  color: #1ab4b4;
  font-family: "Inter", serif;
  font-weight: 500;
}
.reviews {
  color: #888585;
  font-size: 12px;
  text-align: center;
  padding-top: 30px;
}
.social-medias {
  display: flex;
  gap: 20px;
  margin-top: 20px;
}
.w-500 {
  width: 500px;
}
.wrap-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
  align-items: center;
}
.section3-img {
  width: 500px;
}

.section2-img {
  width: 500px;
  padding-top: 40px;
}
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 1200px) {
  .section2-img {
    width: 400px;
    padding-top: 50px;
  }
  .w-500 {
    width: 400px;
  }
  .section2 {
    gap: 15px;
  }

  .section3-img {
    width: 400px;
  }
}

@media only screen and (max-width: 800px) {
  .Home-circle {
    width: 60px;
    height: 60px;
  }
  .w-500 {
    width: 500px;
  }
  .section2-img {
    width: 400px;
    padding-top: 50px;
  }
  .section3-content {
    padding: 30px 0;
  }
  .section2 {
    padding: 30px 0;
  }
  .section3 {
    padding: 30px 0;
  }
  .section3-img {
    width: 400px;
  }
}

@media only screen and (max-width: 500px) {
  .section3-img {
    width: 200px;
  }
  .w-500-1 {
    width: 330px;
  }
  .section2-img {
    width: 400px;
  }
  .section4-box-des {
    width: 300px;
  }
  .section-header {
    font-size: 30px;
  }
  .section-about {
    width: 350px;
  }

  .section1-a {
    height: 70vh;
  }
  .section2-1-details {
    margin: 10px 20px;
  }
  .section2-head {
    font-size: 30px;
  }
  .section2-button {
    height: 40px;
    border-radius: 15px;
  }
  .section2-button p {
    font-size: 16px;
  }
  .section3 {
    padding: 20px 10px;
  }
  .section3-img {
    display: none;
  }
  .buttons {
    gap: 30px;
    padding-top: 30px;
  }
  .button1 {
    width: 120px;
    height: 40px;
    border-radius: 20px;
  }
  .button1 p {
    font-size: 14px;
  }
  .button2 {
    width: 120px;
    height: 40px;
  }
  .button2 p {
    font-size: 14px;
  }
  .social-media {
    gap: 30px;
    margin-top: -30px;
  }
  .sections3-button1 {
    width: 135px;
    height: 50px;
  }
  .Course-detail {
    padding: 0px 25px;
    gap: 10px;
  }
  .sections3-button1 p {
    font-size: 13px;
  }
  .sections3-button2 {
    width: 135px;
    height: 50px;
  }
  .sections3-button2 {
    font-size: 13px;
  }
  .section4-box-head {
    font-size: 30px;
  }
}
